.auth-view {
    width: 100%;
    height: var(--app-height);
    background: #000;
    color: white;
    display: grid;
    grid-template-rows: 1rem 1fr 4rem 8rem;
}

.auth-text {
    color: #808080;
    font-size: 1.2rem;
    display: inline-block;
    text-align: start;
}

.auth-data {
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    margin: 1rem;
}

.bg-black {
    pointer-events: all;
    width: 100%;
    height: 100%;
    background-color: #000;
}

.register-button {
    margin: 0.2em 1em 0.2em 1em;
}