.basic-page-layout {
    display: grid;
    grid-template-rows: 1fr 10rem;
}

.basic-page-layout-no-subscript {
    display: grid;
    grid-template-rows: 1fr 8rem;
}

.basic-content {
    max-height: 100%;
    overflow: auto;
}

.basic-page-button {
    height: 6rem;
}

.basic-page-button-no-subscript {
    height: 2rem
}

.basic-subscript {
    font-weight: 500;
    text-align: center;
    width: 100%;
    color: var(--font-dim);
    font-size: 1.1rem;
}

.basic-subscript-outer {
    height: calc(6rem - var(--button-height));
}