

.login-progress {
    width: 100%;
    height: var(--app-height);
    transition: 0.2s;
    display: grid;
    grid-template-rows: 1fr 2rem 1rem 1fr;
}

.login-wide {
    width: 10rem;
}

.progress-bar {
    height: 0.3rem;
    background: none;
    overflow: hidden;
    border-radius: 0.3rem;
}

.progress-bar-background {
    width: 100%;
    height: 100%;
    background-color: #212121;
    top: 0;
    overflow: hidden;
}