/* White font */
.font-white {
    color: white;
}

/* Gray font */
.font-gray {
    color: var(--font-dim);
}

.font-semi-dim {
    color: var(--font-semi-dim);
}

/* Blue font */
.font-blue {
    color: var(--color-main);
}

.font-green {
    color: var(--color-green);
}

/* Font weight 400 */
.fw-400 {
    font-weight: 400;
}

/* Font weight 500 */
.fw-500 {
    font-weight: 500;
}

/* Font weight 600 */
.fw-600 {
    font-weight: 600;
}

/* Font weight 700 */
.fw-700 {
    font-weight: 700;
}

/* Medium font (1.2rem) */
.fs-m {
    font-size: 1.2rem;
}

/* Small font (1rem) */
.fs-s {
    font-size: 1rem;
}

/* Big font (1.5rem) */
.fs-b {
    font-size: 1.5rem;
}

/* Big font (1.8rem) */
.fs-vb {
    font-size: 1.8rem;
}