.mini-app-navigation {
    position: fixed;
    bottom: 0;
    height: 4rem;
    width: 100%;
    justify-content: center;
    text-align: center;
    display: grid;
    grid-template-columns: 1fr;
    transition: 0.2s;
    z-index: 10;
}

.mini-app-navigation-hidden {
    height: 0;
    transition: 0.2s;
}

.mini-app-navigation-hidden * {
    bottom: -4rem;
    height: 0;
    transition: 0.2s;
}

.navigation-buttons {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    font-size: 2rem;
    margin: auto;
    width: 100%;
}

.nav-button {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    color: #fff;
    height: 4rem;
    width: 100%;
    display: flex;
    justify-content: center;
    justify-items: center;
    align-items: center;
    text-align: center;
    transition: 0.2s;
}