
.progress-bar-button-background {
    width: 100%;
    height: 100%;
    background-color: var(--progress-bar-button-bg);
    top: 0;
    overflow: hidden;
}

.progress-bar-button-content {
    text-align: center;
    justify-content: center;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 600;
}

.progress-bar-button-inner {
    position: absolute;
    border-radius: var(--button-height);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}