/*
  Utility styles (small repeating stuff)
  Like set margin or centering with flex
 */

/* Center element both horizontally and vertically */
.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* flex center in x direction */
.flex-x-center {
    display: flex;
    justify-content: center;
}

/* 1rem margin from left and right direction */
.small-margin {
    margin-left: 1rem;
    margin-right: 1rem;
}

/* 0.2rem top margin */
.mini-margin-top {
    margin-top: 0.2rem;
}

/* 0.2rem bottom margin */
.mini-margin-bottom {
    margin-bottom: 0.2rem;
}

/* 1rem top margin */
.small-margin-top {
    margin-top: 1rem;
}

/* 1rem bottom margin */
.small-margin-bottom {
    margin-bottom: 1rem;
}

/* 1rem left margin */
.small-margin-left {
    margin-left: 1rem;
}

/* 1rem right margin */
.small-margin-right {
    margin-right: 1rem;
}

/* 0.1 margin in all directions */
.micro-margin-all {
    margin: 0.1rem;
}

.small-padding-top {
    padding-top: 1rem;
}

/* 2rem margin from left and right direction */
.big-margin {
    margin-left: 2rem;
    margin-right: 2rem;
}

/* Width 100% */
.full-wide {
    width: 100%;
}

.flex-right {
    display: flex;
    justify-content: right;
}

/* Height 100% */
.full-high {
    height: 100%;
}

/* Width 100% Height 100% */
.full-size {
    width: 100%;
    height: 100%;
}

/* Place element in the bottom of a container using flex */
.flex-bottom {
    display: flex;
    align-items: end;
}

/* Place element in the top of a container using flex */
.flex-top {
    display: flex;
    align-items: start;
}

/* Center element in y axis */
.flex-y-center {
    display: flex;
    align-items: center;
}

/* Display elements from end horizontally */
.flex-x-right {
    display: flex;
    justify-content: right;
}

/* No word break */
.no-break {
    white-space: nowrap;
}

.ellipsis {
    text-overflow: ellipsis;
}

.spinning {
    animation: rotate-animation 1s infinite linear;
}

@keyframes rotate-animation {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

/* Cursor hover change */
.clickable {
    cursor: pointer;
}

/* Hide the content */
.hidden {
    display: none;
}

.top-absolute {
    position: absolute;
    top: 0;
}

/* Font size 1.2rem */
.medium-font {
    font-size: 1.2rem;
}

/* Font size 1.5rem */
.big-font {
    font-size: 1.5rem;
}

/* Font size 2rem */
.huge-font {
    font-size: 2.5rem;
}

.arial-rounded {
    font-family: "ArialRounded", serif;
}

.text-center {
    text-align: center;
}

.text-left {
    text-align: left;
}

/* Align items in column via flex */
.flex-column {
    display: flex;
    flex-direction: column;
}

.flex-row {
    display: flex;
    flex-direction: row;
}

.small-gap {
    gap: 1rem;
}

.tiny-gap {
    gap: 0.25rem;
}

.full-screen {
    top: 0;
    position: fixed;
    left: 0;
    height: var(--app-height);
    width: 100%;
}

.click-through {
    pointer-events: none;
}

.scrollable-data-column {
    margin-top: 1rem;
    padding-bottom: 2rem;
    display: flex;
    overflow: auto;
    flex-direction: column;
    gap: 1.5rem;
}