/* Upper-level style for list entry markup */
.list-entry {
    --image-dimentions: 5rem;
    
    display: grid;
    grid-template-columns: var(--image-dimentions) 1fr;
}

.list-entry-middle {
    display: grid;
    grid-template-rows: 1fr 1fr;
    min-width: 10ch;
}

.list-entry-bg {
    background: var(--bg-gray);
    border-radius: 1rem;
}

.list-entry-bg-outline {
    background: none;
    border-color: var(--bg-gray);
    border-style: solid;
    border-width: 1px;
    border-radius: 1rem;
}

.list-entry-tip-outer {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background: none;
    border-color: var(--bg-gray);
    border-style: solid;
    border-width: 1px;
    border-radius: 1rem;
    width: calc(100% - 2rem);
    color: var(--font-dim);
    display: flex;
    align-items: center;
    align-content: center;
}

.list-entry-tip-span {
    text-align: center;
    font-size: 1.2rem;
    margin: auto 1rem;
}

/* Image in a circle with radius of */
.circle-image {
    height: calc(var(--image-dimentions) - 2rem);
    width: calc(var(--image-dimentions) - 2rem);
    border-radius: var(--image-dimentions);
}

.square-image {
    height: calc(var(--image-dimentions) - 2rem);
    width: calc(var(--image-dimentions) - 2rem);
    border-radius: calc(var(--image-dimentions) / 7);
    overflow: hidden;
}

.square-image-outer {
    height: var(--image-dimentions);
    width: var(--image-dimentions);
    border-radius: var(--image-dimentions);
}

.entry-light-bg {
    background-color: var(--bg-gray);
    border-radius: 1rem;
}

.list-header {
    color: var(--font-bright);
    font-size: 1.6rem;
    font-weight: 400;
}