.navigation-screen {
    position: relative;
    grid-column: 1;
    grid-row: 1;
    overflow: hidden;
    width: 100vw;
}

.navigation-stack {
    max-width: 100%;
    max-height: 100%;
    overflow: hidden;
    width: 100%;
    display: grid;
    touch-action: none;
}

.navigation-stack * {
    touch-action: pan-y;
}