.user-profile-edit {
    display: grid;
    grid-template-rows: 7rem 1fr;
}

.circle-image-big {
    height: 5rem;
    width: 5rem;
    border-radius: 5rem;
}

.user-profile-edit-content {
    margin-top: 1rem;
    padding-bottom: 2rem;
    display: flex;
    overflow: auto;
    flex-direction: column;
    gap: 1.5rem;
}