.user-account-search {
    display: grid;
    grid-template-columns: 1fr 2rem;
    padding: 0.5rem 0.5rem 0.5rem 1rem;
    border-radius: 2.5rem;
    border: 1px solid #5E5E5E;
    height: 2rem;
}

.user-account-search-input {
    outline: none;
    background-color: black;
    font-size: 1.2rem;
    font-weight: 500;
    color: white;
    border: none;
}

.user-account-search-input:focus {
    border: none;
}

.user-account-search-input::placeholder {
    color: var(--font-dim);
}

.transfer-input {
    outline: none;
    background-color: black;
    font-size: 3rem;
    font-weight: 500;
    color: white;
    border: none;
}

.user-account-search-input:focus {
    border: none;
}

.user-account-search-input::placeholder {
    color: var(--font-dim);
}