.loading-placeholder {
    background: #222222;
    text-align: center;
}

.l-rounded {
    border-radius: 1.5rem;
}

.l-margin {
    margin: 0.2rem;
}

.loading-balance-span {
    width: 20rem;
    height: 2.7rem;
}

.loading-telegram-name {
    width: 8rem;
    height: 1.5rem;
}

.loading-center {
    display: flex;
    text-align: center;
    justify-content: center;
    justify-items: center;
    width: 100%;
}

.loading-invite-friend-button {
    width: 100%;
    height: var(--button-height);
    margin: 0 0.35rem 0 0;
}

.loading-friends-claim-button {
    width: 100%;
    height: var(--button-height);
    margin: 0 0 0 0.35rem;
}

.loading-friends-board-header {
    height: 2rem;
    width: 14rem;
    margin: 1rem 0 0 1rem
}

.loading-button-subscript {
    height: 1.3rem;
    width: 20rem;
}

.loading-circle-big {
    width: 2rem;
    height: 20px;
}

.loading-entry-text {
    height: 1.2rem;
}

.loading-entry-text-big {
    height: 2rem;
}

.loading-entry-subscript {
    background: #303030;
    height: 1rem;
}


@keyframes placeHolderShimmer{
    0%{
        background-position: -800px 0
    }
    100%{
        background-position: 800px 0
    }
}

.loading-animated-background-bright {
    animation-duration: 5s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: linear;
    background: darkgray;
    background: linear-gradient(to right, #282828 10%, #323232 18%, #282828 33%);
    background-size: 800px 104px;
}