body {
    /* Custom */
    --button-height: 3.5rem;
    --button-radius: calc(var(--button-height) / 2);
    --app-height: var(--tg-viewport-height);

    /* Colors */
    --font-bright: white;
    --font-dim: #606060;
    --font-semi-dim: #808080;
    --font-blue: #207BF9;
    --bg-black: black;
    --bg-gray: #212121;
    --progress-bar-button-bg: #1E1E22;
    --rate-bar-color: #5E5E5E;

    --color-blue: #207BF9;
    --color-main: #35C170;
    --color-red: #FC302C;
    --color-orange: #FB7429;
    --color-yellow: #F6ED1E;
    --color-green: #26EF5E;

    --paywall-plan-bg: #131313;

    --system-font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif, "Inter";

    /* CommonOld */
    margin: 0;
    overscroll-behavior: none;
    background-color: #000000;
    font-family: var(--system-font), sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: transparent;
    touch-action: none !important;
}

@font-face {
    font-family: 'ArialRounded';
    src: local('ArialRounded'), url(fonts/arialroundedmtbold.ttf);
}

/* Disable scrollbars */
html {
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
    /* Internet Explorer 10+ */
    scrollbar-width: none;
    /* Firefox */
}

html::-webkit-scrollbar {
    display: none;
    /* Safari and Chrome */
}