.catalog-select-button {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    text-align: left;
    font-size: 1.5rem;
    color: #808080;
}

.catalog-select-button-selected {
    color: #ffffff;
}

.catalog-select-buttons {
    overflow-x: scroll;
    margin-left: 1rem;
    margin-right: 1rem;
    width: calc(100% - 2rem);
    gap: 3rem;
    display: flex;
    flex-direction: row;
    touch-action: pan-x;
}

.catalog-select-buttons {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}

.catalog-select-buttons::-webkit-scrollbar {
    display: none;
}