.small-popup-outer {
    background-color: rgba(0, 0, 0, 0.7);
    pointer-events: all;
    position: absolute;
    height: var(--app-height);
    width: 100vw;
    top: 0;
    left: 0;
    transition: 0.3s;
}

.small-popup-outer-hidden {
    background-color: rgba(0, 0, 0, 0);
    pointer-events: none;
    transition: 0.3s;
}

.small-popup-content {
    background-color: var(--progress-bar-button-bg);
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
    opacity: 1;
    width: calc(100vw - 4rem);
    transition: 0.3s;
}

.small-popup-content-hidden {
    opacity: 0;
    transition: 0.3s;
}

@media only screen and (min-width: 30rem) {
    .small-popup-content {
        width: 30rem;
    }
}

.vertical-hidden {
    bottom: 0;
    height: 0;

    transition: 0.5s;
}

.rate-jetton-header {
    display: grid;
    grid-template-columns: 2rem 1fr 2rem;
}

.cancel-cross-outer {
    width: 2rem;
    height: 2rem;
}
