/* Override button styles */
.empty-button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
}

/* container with fixed height and width of 100% */
.button-outer {
    height: var(--button-height);
    width: 100%;
}

/* rounded button fill */
.wa-button {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: var(--button-radius);
}

/* Usual text for button */
.wa-button-text {
    font-size: 1.2rem;
    font-weight: 600;
}